body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  min-width: 400px;
}
body section {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  flex-wrap: wrap;
  width: 1440px;
  margin-top: 10vh;
}
body section img {
  min-width: 360px;
  width: 35.0694444444vw;
  margin: 1rem;
}
body section article {
  width: 26vw;
  min-width: 270px;
  color: #121212;
}
body section article header h1 {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  font-size: 2.62rem;
  line-height: 120%;
  margin-bottom: 1.5rem;
}
body section article p {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 300;
  font-size: 1rem;
  line-height: 150%;
  margin-bottom: 2rem;
}
body section article button {
  border: none;
  background: #e4c1ae;
  border-radius: 32px;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  padding: 16px 40px;
  gap: 10px;
  color: #ffffff;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  font-size: 1rem;
  line-height: 100%;
}
body section article button:hover {
  filter: brightness(80%);
  cursor: pointer;
}
body aside {
  display: none;
  position: absolute;
  width: 100vw;
  height: 100vh;
  min-width: 400px;
  background-color: rgba(0, 0, 0, 0.644);
}
body aside .content {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: white;
  width: 63vw;
  padding: 6.5972222222vw;
  padding-top: 5.8333333333vh;
  padding-bottom: 5.8333333333vh;
}
body aside .content h2 {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  font-size: 2.8rem;
  line-height: 120%;
  margin-bottom: 1.5rem;
  margin-top: 0;
}
body aside .content p {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 300;
  font-size: 1rem;
  line-height: 150%;
}
body aside .content .close-btn {
  position: absolute;
  right: 2rem;
  top: 2rem;
  padding: 0;
  line-height: 1rem;
  font-size: 1.5rem;
  font-weight: 600;
  border: none;
  background-color: white;
  cursor: pointer;
}
body aside .content .setNull-btn {
  display: none;
  border: none;
  background: #e4c1ae;
  border-radius: 32px;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  padding: 16px 40px;
  gap: 10px;
  color: #ffffff;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  font-size: 1rem;
  line-height: 100%;
}
body aside .content .setNull-btn:hover {
  filter: brightness(80%);
  cursor: pointer;
}